import { Flex, Skeleton, TableProps, TableRowProps, Tbody, Td, Tr } from '@chakra-ui/react'
import { ReactNode } from 'react'
interface TableLoadingProps<T> extends TableProps {
  tableListLength: number
  tdListLength: number
  minW?: string
  firstTd?: ReactNode
  rowStyle?: TableRowProps | ((record: T, index?: number) => TableRowProps)
}
export default function TableLoading<T>(props: TableLoadingProps<T>) {
  const { rowStyle, minW, tableListLength, tdListLength, firstTd } = props
  const tableList = new Array(tableListLength).fill(0)
  const tdList = new Array(tdListLength).fill(0)

  return (
    <Tbody>
      {tableList?.map((item: any, index: any) => {
        return (
          <Tr
            key={item + `${index}`}
            sx={{
              td: {
                _last: {
                  '>div': {
                    justifyContent: 'flex-end'
                  }
                }
              }
            }}
            {...(typeof rowStyle === 'function' ? rowStyle(item, index) : rowStyle)}
          >
            <>
              {firstTd && <Td>{firstTd}</Td>}
              {tdList?.map((item: any, index: any) => {
                return (
                  <Td key={index}>
                    <Flex w="100%" justifyContent={!firstTd && index == 0 ? 'flex-start' : 'flex-end'}>
                      <Skeleton />
                    </Flex>
                  </Td>
                )
              })}
            </>
          </Tr>
        )
      })}
    </Tbody>
  )
}
