import { Box, Center, FlexboxProps, StackProps, VStack } from '@chakra-ui/react'
import Pagination, { PaginationProps } from '../Pagination'
import { ColumnsType } from '../Table'
import H5MapTableItem from './H5MapTableItem'
import { transColumns2H5 } from './util'

interface H5MapTableType<T> {
  dataSource: T[]
  itemSkeletonLength?: number
  skeletonLength?: number
  columns: ColumnsType<T>[]
  rowKey?: string | ((record: T) => string)
  onRowClick?: (record: T, index?: number) => void
  rowStyle?: StackProps | ((record: T, index: number) => StackProps)
  loading: boolean
  pagination?: PaginationProps
  wrapStyle?: StackProps
  itemHeight?: FlexboxProps['flexBasis']
}

function H5MapTable<T>({
  dataSource,
  columns,
  itemSkeletonLength,
  skeletonLength = 3,
  rowKey,
  loading,
  pagination,
  rowStyle,
  wrapStyle,
  onRowClick,
  itemHeight
}: H5MapTableType<T>) {
  const skeletonData = new Array(skeletonLength).fill(0)
  return (
    <VStack w="100%" {...(wrapStyle || {})}>
      {(dataSource && dataSource?.length && !loading ? dataSource : skeletonData)?.map((item, index) => (
        <VStack w="100%" key={loading ? index : typeof rowKey === 'function' ? rowKey(item) : rowKey ? (item as unknown as any)[rowKey] : index}>
          <H5MapTableItem
            key={loading ? index : typeof rowKey === 'function' ? rowKey(item) : rowKey ? (item as unknown as any)[rowKey] : index}
            data={transColumns2H5<T>(columns, item, index)}
            loading={loading}
            skeletonLength={itemSkeletonLength}
            onClick={() => onRowClick?.(item, index)}
            style={typeof rowStyle === 'function' ? rowStyle(item, index) : rowStyle}
            itemHeight={itemHeight}
          />
          <Box w="100%" h="1px" bg="border" mt="12px" />
        </VStack>
      ))}
      {pagination && pagination?.total > pagination?.size && (
        <Center mt="16px">
          <Pagination {...pagination} />
        </Center>
      )}
    </VStack>
  )
}

export default H5MapTable
