import {
  Center,
  Table as CKTable,
  TableBodyProps,
  TableCellProps,
  TableColumnHeaderProps,
  TableContainer,
  TableHeadProps,
  TableProps,
  TableRowProps,
  Tbody,
  Td,
  Th,
  Thead,
  Tr
} from '@chakra-ui/react'
import { Key, ReactNode } from 'react'
import Pagination, { PaginationProps } from './Pagination'
import TableLoading from './TableLoading'

export type ColumnsType<T> = {
  title: ReactNode | ((...args: any) => ReactNode)
  render?: ReactNode | ((record: T, index?: number) => ReactNode)
  key: Key | ((...args: any) => Key)
  hidden?: any
  showLabel?: boolean
  thConfig?: TableColumnHeaderProps
  tdConfig?: TableCellProps
}

interface TableType<T> extends TableProps {
  dataSource: T[]
  skeletonLength?: number
  columns: ColumnsType<T>[]
  rowKey?: string | ((record: T) => string)
  onRowClick?: (record: T, index?: number) => void
  rowStyle?: TableRowProps | ((record: T, index?: number) => TableRowProps)
  loading: boolean
  tdBorder?: boolean
  firstTdSkeleton?: ReactNode | ((...args: any) => ReactNode)
  theadConfig?: TableHeadProps
  tbodyConfig?: TableBodyProps
  pagination?: PaginationProps
}
function Table<T>({
  dataSource,
  skeletonLength = 3,
  columns,
  rowKey,
  loading,
  tdBorder,
  firstTdSkeleton,
  theadConfig,
  tbodyConfig,
  rowStyle = {},
  onRowClick,
  pagination,
  ...args
}: TableType<T>) {
  return (
    <>
      <TableContainer w="100%">
        <CKTable size="sm" {...args}>
          <Thead {...theadConfig}>
            <Tr>
              {columns.map(({ title, key, thConfig }) => {
                return (
                  <Th key={typeof key === 'function' ? key() : key} {...thConfig}>
                    {typeof title === 'function' ? title() : title}
                  </Th>
                )
              })}
            </Tr>
          </Thead>
          {loading ? (
            <TableLoading<T>
              tableListLength={skeletonLength}
              tdListLength={columns.length}
              firstTd={typeof firstTdSkeleton === 'function' ? firstTdSkeleton() : firstTdSkeleton}
              rowStyle={rowStyle}
            />
          ) : (
            <Tbody {...tbodyConfig}>
              {dataSource?.map((item: any, index) => {
                return (
                  <Tr
                    key={rowKey ? (typeof rowKey === 'function' ? rowKey(item) : (item as any)[`${rowKey}`]) : (item as any)?.key || index}
                    borderBottom={tdBorder ? '1px solid' : 'none'}
                    borderColor={tdBorder ? 'border' : 'transparent'}
                    onClick={() => onRowClick && onRowClick(item, index)}
                    {...(typeof rowStyle === 'function' ? rowStyle(item, index) : rowStyle)}
                  >
                    {columns.map(({ render, key, tdConfig }) => {
                      return (
                        <Td key={key as any} {...tdConfig} fontWeight="500">
                          {typeof render === 'function' ? render(item, index) : render || item[key as string]}
                        </Td>
                      )
                    })}
                  </Tr>
                )
              })}
            </Tbody>
          )}
        </CKTable>
      </TableContainer>
      {pagination && pagination?.total > pagination?.size && (
        <Center mt="16px">
          <Pagination {...pagination} />
        </Center>
      )}
    </>
  )
}

export default Table
